<template>
  <div>
    <b-form-group>
      <label for="publicationTitle">Publication Title</label>
      <b-form-input
      type="text"
      id="publicationTitle"
      required
      v-model="data.publicationTitle"></b-form-input>
    </b-form-group>

    <b-form-group>
      <label for="editorial">Editorial</label>
      <b-form-input
      type="text"
      id="editorial"
      required
      v-model="data.editorial"></b-form-input>
    </b-form-group>

    <b-form-group>
      <label for="publicationDate">Publication Date</label>
      <b-form-datepicker
      id="publicationDate"
      required
      v-model="data.publicationDate"></b-form-datepicker>
    </b-form-group>

    <b-form-group>
      <label for="publicationURL">Publication URL</label>
      <b-form-input
      type="url"
      id="publicationURL"
      required
      v-model="data.publicationURL"></b-form-input>
    </b-form-group>
  </div>
</template>

<script>
/* eslint-disable */

import Vue from 'vue';
import { FormDatepickerPlugin, FormInputPlugin, FormGroupPlugin } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

Vue.use(FormInputPlugin);
Vue.use(FormGroupPlugin);
Vue.use(FormDatepickerPlugin);

export default {
  name: 'PublicationForm',
  props: [
    'data',
  ],
}
</script>



